import React from 'react';
import './Facedetection.css';

const Facedetection = ({imageURL, box}) =>{
	return(
		<div className="imageEdit">
		   <img id="testImage" alt="" src={imageURL}/>
		   {/*{console.log(box.top , box.bottom, box.left , box.right)}*/}
		   <div className ="editBox">
		   <div className = "theBox"  style={{top: box.top, bottom: box.bottom, right: box.right, left: box.left}}>
		   </div>
		   </div>
		</div>
		)
}

export default Facedetection;