import React from "react";
import './Navigation.css';

const Navigation = ({route, onSignOut}) => {
	return(
		<div>
			{(route === "home")
				?<div>
					<div className ="editNav">
						<p className="editNavcontent" onClick={ () => onSignOut("signin") }>Sign out</p>
					</div>
				</div>
				:<div></div>
			}
		</div>
	)
}

export default Navigation;
