import React from 'react';
import './InputForm.css';



const InputForm = ({onInputEvent,onButtonEvent, firstName, entries}) =>{
	return(
		<div className="editInputForm">
			<div className= "header">
				<h1>Welcome!,{`${firstName}`}</h1>
				<p className="paragraph">This is a website which detects face of an image for you</p>
				<p className="paragraph" id='para'>{`your entry count is ${entries}`}</p>
			</div>
			<div className= "manageContent">
				<div className= "inputFormContent">
					<input className="detectInput" placeholder= "Enter url of the image" onChange={onInputEvent}/>
					<button className="detectButton" onClick={onButtonEvent}>Detect</button>
				</div>
			</div>
		</div>
		)
}


export default InputForm;