import React from 'react';
import "./Signin.css";

class Signin extends React.Component {
	constructor(props){
	 	super(props);
	 	this.state ={
	 		emailInput :'',
	 		passwordInput :''
		}
	 }
	onEmailInput = (event) =>this.setState({emailInput : event.target.value});
	onPasswordInput = (event) =>this.setState({passwordInput : event.target.value});

	onSubmit = () =>{
	 	fetch("https://intense-waters-90940.herokuapp.com/signin" ,
	 		{
	 			method: "post",
	 			headers: {'Content-Type':'application/json'},
	 			body: JSON.stringify({
	 				email: this.state.emailInput,
	 				password: this.state.passwordInput
	 			})
	 		})
	 	.then(response => response.json())
	 	.then(user => {
			if(user.id){ 
				this.props.loadUser(user);
	 			this.props.onSignin('home');
	 		}
	 		else{
	 			this.props.onSignin('signin');
	 			console.log('error logging in');
	 		}
	 	})
	  }

	render(){
		return(
		<div className="outline">
		 	<div className ="form">
		 		<h1 className= "signin">Sign in</h1>
		 		<form>
		 			<div className="username_Password_text_Box">
		 				<div className="username_Password">
		 					Email: 
		 					Password:
		 				</div>
		 				<div className="text_Box">
		 					<input onChange ={this.onEmailInput} type="email" className="inputEdit"/><br/>
		 					<input onChange ={this.onPasswordInput} type = "password" className="inputEdit"/><br/>
		 				</div>
		 			</div><br/>
					<input type="button" value="Sign in" className="buttonedit" onClick={this.onSubmit}/><br/>
					<div className="editRegister">
						<p className ="register" onClick ={() =>this.props.onSignin("register")}>register</p>
					</div>		 		
				</form>
			</div>
		</div>
		)
	}
}

export default Signin;
