import React, { Component } from 'react';
import Navigation from './components/navigation/Navigation.js';
import Logo from './components/logo/Logo.js';
import Particles from 'react-particles-js';
import Signin from './components/signin/Signin.js';
import Clarifai from 'clarifai';
import Facedetection from './components/facedetection/Facedetection.js';
import './App.css'
import './components/signin/Signin.css';
import InputForm from './components/InputForm/InputForm.js';
import Register from './components/Register/Register.js';

const parameters = {
                    particles: {
                    number: {
                            value: 200,
                            density:{
                              enable: true,
                              value_area: 800
                            }
                          }
                    }
                  }


const app = new Clarifai.App({
 apiKey: 'efe6234fca264fe8a756f7ec0a89a287'
});

class App extends Component {
  constructor(){
    super();
    this.state={
    textInput:"",
    imageURL:"",
    box: {},
    route: "signin",
    user: {
      id:"",
      firstName: "",
      lastName: "",
      email: "",
      password: "",
      entries: '',
      joined: ''
    }
    }
  }

  loadUser = (data) =>{
    this.setState({
      user: {
        id : data.id,
        firstName: data.FirstName,
        lastName: data.LastName,
        email : data.email,
        entries : data.entries,
        joined: data.joined
      }
    })
  }

  obtainBox = (response) => {
    const image=document.getElementById("testImage");
    const detector=response.outputs[0].data.regions[0].region_info.bounding_box;
    return{
       top :  (detector.top_row * image.height),
       bottom : image.height - (detector.bottom_row * image.height),
       left : (detector.left_col * image.width),
       right : image.width - (detector.right_col * image.width)
      }
  }

  displayBox = (box) => {
    this.setState({box:box});
  } 


  onInputEvent = (event) =>{
    this.setState({textInput: event.target.value});
  }

  onButtonEvent = () =>{
    this.setState({imageURL: this.state.textInput});
    app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.textInput)
    .then(response => 
    {
      this.displayBox(this.obtainBox(response))
      if(response){
        fetch('https://intense-waters-90940.herokuapp.com/entrycount', {
          method: 'put',
          headers: {'content-type' : 'application/json'},
          body: JSON.stringify({
            id: this.state.user.id
          })
        })
        .then(response => response.json())
        .then(count => this.setState(Object.assign(this.state.user, {entries:count})))
      }
    })
    .catch(err => console.log(err))
    console.log(this.state.user.id);
  }

  onSignin = (route) =>{
    if(route == "signin"){
      this.setState({
        box: {},
        imageURL: ""
      });
    }
    this.setState({route: route});
  }

  render() { 
    return (
      <div className="App">
         <Particles params={parameters} className ="editParticles"/>
         <Navigation route={this.state.route} onSignOut={this.onSignin}/>
          {this.state.route === "signin"
            ?<div><Signin onSignin={this.onSignin} loadUser = {this.loadUser}/></div>
            :(this.state.route === "home")
              ?<div>
                <Logo/>
                <InputForm 
                  onInputEvent= {this.onInputEvent}
                  onButtonEvent= {this.onButtonEvent}
                  firstName= {this.state.user.firstName}
                  entries= {this.state.user.entries}
                />
                <Facedetection box ={this.state.box} imageURL={this.state.imageURL} entries/>
              </div>
              :(this.state.route === "register")
                ?<Register onRegister={this.onSignin}/>
                :<div></div>
         }
      </div>
    );
  }
}

export default App;
