import React,{Component} from "react";
import '../signin/Signin.css';

class Register extends Component {
	constructor(props){
		super(props);
		this.state = {
			firstNameInput:'',
			lastNameInput:'',
			emailInput:'',
			passwordInput:''
		}
	}

	onfirstName = (event) =>{ this.setState({firstNameInput: event.target.value})};
	onlastName = (event) =>{ this.setState({lastNameInput: event.target.value})};
	onEmail = (event) =>{ this.setState({emailInput: event.target.value})};
	onPassword = (event) =>{ this.setState({passwordInput: event.target.value})};

	onSubmit = () => {
		fetch("https://intense-waters-90940.herokuapp.com/register", 
		{
			method :'post',
			headers : {'Content-type' : 'application/json'},
			body : JSON.stringify({
				firstName : this.state.firstNameInput,
				lastName : this.state.lastNameInput,
				email : this.state.emailInput,
				password : this.state.passwordInput,
			})
		})
		.then( response => response.json())
		.then( user => 
		{
			console.log(user);
			console.log("here");
			this.props.onRegister('signin');
		})
	}

	render(){
		return(
		<div>
          <div className="outline">
            <div className ="form">
              <h1 className="signin">Register</h1>
                <div className="username_Password_text_Box">
                  <div className="username_Password">
                    First Name:<br/> 
                    Last Name:<br/>
                    Email:<br/>
                    Password:
                  </div>
                  <div className="text_Box">
                    <input type="text" className="inputEdit" onChange ={this.onfirstName}/><br/>
                    <input type = "text" className="inputEdit" onChange ={this.onlastName}/><br/>
                    <input type="email" className="inputEdit" onChange ={this.onEmail}/><br/>
                    <input type = "password" className="inputEdit" onChange ={this.onPassword}/><br/>
                  </div>
                </div><br/>
                <input type="button" value="register" className="buttonedit" onClick = {this.onSubmit}/><br/> 
                <div className="editRegister">
					<p className ="register" onClick ={() =>this.props.onRegister("signin")}>sign in</p>
				</div>       
            </div>
          </div>
        </div>
		)
	}
}

export default Register;